// will be used in the future
import Tracker from '@openreplay/tracker';
import trackerFetch from '@openreplay/tracker-fetch';
import trackerRedux from '@openreplay/tracker-redux';
import { createMiddleware } from 'redux-beacon';
import { api, checkout } from '../actions';

export function patchFetch(tracker) {
    window.fetch = tracker.use(trackerFetch());
}

export function openReplayMiddleware(tracker) {
    return tracker.use(trackerRedux());
}

export const OpenReplayTarget = ({ tracker }) => (events) => {
    events.forEach(({ event: eventType, identify, issue, metadata, ...payload }) => {
        if (identify) {
            tracker.setUserID(identify);
        }
        if (metadata) {
            Object.entries(metadata).forEach(([key, value]) => {
                tracker.setMetadata(key, value);
            });
        }
        if (eventType) {
            tracker.event(eventType, payload, !!issue);
        }
    });
};

export const eventsMap = {
    [api.auth.login.successType]: () => ({
        event: 'login',
        method: 'email',
    }),
    [api.users.create.successType]: () => ({
        event: 'sign_up',
        method: 'email',
    }),
    [api.websites.update.successType]: (action, prevState, nextState) => {
        if (
            !nextState.websites.find((website) => website.slug === action.payload.slug)?.enabled &&
            action.payload.enabled
        ) {
            return {
                event: 'website_published',
            };
        }
    },
    [api.listenSubscriptionCreated.type]: (action) => ({
        event: 'subscription_created',
        ...action.payload,
    }),
    [api.listenSubscriptionCancelled.type]: () => ({
        event: 'subscription_cancelled',
    }),
    [api.users.me.successType]: (action) => ({
        identify: action.result.email ?? action.result._id,
        metadata: {
            email: action.result.email,
            userId: action.result._id,
        },
    }),
    [api.workspaces.retrieve.successType]: (action) => ({
        metadata: {
            workspaceId: action.result._id,
            plan: action.result.billing.plan,
        },
    }),
    [checkout.applyCoupon.type]: (action) => ({
        event: 'checkout.applyCoupon',
        coupon: action.payload.coupon,
    }),
    [checkout.changeCountry.type]: (action) => ({
        event: 'checkout.changeCountry',
        country: action.payload.country,
    }),
    [checkout.close.type]: () => ({
        event: 'checkout.close',
    }),
    [checkout.complete.type]: () => ({
        event: 'checkout.complete',
    }),
    [checkout.completePayment.type]: () => ({
        event: 'checkout.completePayment',
    }),
    [checkout.load.type]: () => ({
        event: 'checkout.load',
    }),
    [checkout.error.type]: (action) => ({
        event: 'checkout.error',
        error: action.payload.error,
        issue: true,
    }),
    [checkout.selectPaymentMethod.type]: (action) => ({
        event: 'checkout.selectPaymentMethod',
        paymentMethod: action.payload.payment_method,
    }),
};

const logger = process.env.NODE_ENV === 'development' ? require('@redux-beacon/logger').default : null;

export function OpenReplay({ projectKey }) {
    const tracker = new Tracker({ projectKey });
    patchFetch(tracker);
    const openReplayMiddleware = tracker.use(trackerRedux());
    const openReplayEventsTrackerMiddleware = createMiddleware(eventsMap, OpenReplayTarget({ tracker }), { logger });
    tracker.start();
    tracker.setMetadata('hostname', window.location.hostname);
    return { openReplayMiddleware, openReplayEventsTrackerMiddleware };
}
